.card > h1 {
    font-size: 20px;
    padding: 5px;
}

.card > p {
    padding: 5px;
    overflow: auto;
}

a:focus, a:hover {
    color: #397ab7;
    text-decoration: none;
}

div.card-icon > i.fa-trash {
    position: absolute;
    top: 1rem;
    right: 2rem;
    z-index: 9999999;
    cursor: pointer;
    color: #338dcd;
}

div.card-icon > i.fa-edit {
    position: absolute;
    top: 1rem;
    right: 5rem;
    z-index: 9999999;
    cursor: pointer;
    color: #338dcd;
}

.card-icon {
    position: relative;
}
.container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: auto;
    grid-gap: 15px;
    width: 100%;
    padding: 15px;
}

.card {
    width: 100%;
    border: 1px solid black;
    background: #f7f7f7;
    height: 200px;
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 15px;
}

.card-icon {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    height: 200px;
    position: relative;
}


@media only screen and (min-width:560px) {

    .container {
        grid-template-columns: repeat(2, 1fr);
    }

}

@media only screen and (min-width:1140px) {
    .container {
        grid-template-columns: repeat(3, 1fr);
    }
}
